<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }

  .order-id:hover {
    color: #409EFF;
  }

  .sp-handel-w {
    padding-left: 18px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ececec;
  }

</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <region-city-store @StoreGroupChange="StoreGroupChange"></region-city-store>

        <el-form-item label="所属团长店">
          <el-select class="search-input" v-model="searchContent.storeLeaderId" filterable placeholder="请选择所属团长店" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in storeLeaderOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="订单来源">
          <el-select class="search-input" v-model="searchContent.platformId" filterable placeholder="请选择订单来源" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in platformIdOptions" :key="item.platformId" :label="item.name"
              :value="item.platformId">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务类型">
          <el-select class="search-input" v-model="searchContent.businessType" filterable placeholder="请选择业务类型"
            clearable size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in businessTypeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="配送类型">
          <el-select class="search-input" v-model="searchContent.orderType" filterable placeholder="请选择配送类型" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in orderTypeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="订单状态">
          <el-select class="search-input" v-model="searchContent.orderStatus" placeholder="请选择订单状态" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option :label="item.value" :value="item.code" :key="item.code" v-for="item in orderStatusOptions">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="下单时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            :clearable="false"
            :editable="false"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>

        <el-form-item label="订单号">
          <el-input v-model.trim="searchContent.orderId" size="small" class="search-input" placeholder="请输入订单号"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="收货人手机号" v-if="showHignSearch">
          <el-input v-model.trim="searchContent.rTel" size="small" class="search-input" placeholder="收货人手机号"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="SKU编码/商品名称" size="small"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="退款类型" v-if="showHignSearch">
          <el-select class="search-input" v-model="searchContent.refundType" filterable placeholder="请选择退款类型" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in refundTypeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="开始提货/配送日期" v-if="showHignSearch">
          <el-date-picker class="search-input" v-model="searchContent.receiveTimeStart" type="date" placeholder="开始提货/配送日期"
            format="yyyy-MM-dd" value-format="yyyy-MM-dd" size="small" @change="doSearchHandel(true)">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="同步ERP状态" v-if="showHignSearch">
          <el-select class="search-input" v-model="searchContent.syncErpStatus" placeholder="请选择订单状态" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in syncErpStatusOptions">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否群接龙订单" v-if="showHignSearch">
          <el-select class="search-input" v-model="searchContent.solitaireType" placeholder="是否群接龙订单" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in solitaireTypeOptions">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-plus" @click="showHignSearch = true" size="mini" v-if="!showHignSearch">高级查询</el-button>
          <el-button type="primary" icon="el-icon-minus" @click="showHignSearch = false" size="mini" v-if="showHignSearch">收起高级查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="exportData" size="mini" :loading="exportLoading">导出订单</el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="exportOrderAndGoodsData" size="mini" :loading="exportLoadingForGoods">导出订单商品</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button size="mini" class="sup-mini" :disabled="checkedList.length === 0" icon="el-icon-refresh"
          @click="doBatchRePushHandel">重新推送ERP</el-button>
      </div>
      <el-table class="fixed-height" v-loading="loading" :data="tableData" @select="handleSelect"
        @select-all="handleSelectAll" empty-text="没有查询到您想要的数据" style="width:100%;" size="small">
        <el-table-column type="selection" align="center">
        </el-table-column>
        <el-table-column min-width="120px" prop="platformName" label="订单来源"></el-table-column>
        <el-table-column min-width="220px" prop="orderId" label="订单号">
          <template slot-scope="scope" size="small">
            <p class="order-id">
              <el-tag size="mini" v-if="scope.row.parentOrderNo">子单</el-tag>
              <el-tag size="mini" v-else type="success">母单</el-tag>
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </p>
          </template>
        </el-table-column>
        <el-table-column min-width="180px" prop="orderId" label="母单号">
          <template slot-scope="scope" size="small">
            <p>
              {{scope.row.parentOrderNo ? scope.row.parentOrderNo : scope.row.orderId}}
            </p>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="businessTypeName" label="业务类型" :formatter="businessTypeNameFormater">
        </el-table-column>
        <el-table-column min-width="86px" prop="orderTypeName" label="配送类型"></el-table-column>
        <el-table-column min-width="130px" prop="erpStoreName" label="所属门店"></el-table-column>
        <el-table-column min-width="130px" label="所属团长店">
          <template slot-scope="scope" size="small">
            <p v-if="scope.row.storeLeaderId">{{scope.row.storeLeaderId}}_{{scope.row.virtualStoreName}}_{{scope.row.userName}}</p>
            <p v-else>-</p>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="rName" label="收货人"></el-table-column>
        <el-table-column min-width="120px" prop="rTel" label="收货手机"></el-table-column>
        <el-table-column min-width="86px" prop="orderTotalAmount" label="订单金额" :formatter="moneyToFixed">
        </el-table-column>
        <el-table-column min-width="86px" prop="realPaidAmount" label="实付金额" :formatter="moneyToFixed">
        </el-table-column>
        <el-table-column min-width="86px" prop="refundAmount" label="退款金额" :formatter="moneyToFixed">
        </el-table-column>
        <el-table-column min-width="86px" prop="deliveryAmount" label="配送费" :formatter="moneyToFixed"></el-table-column>
        <el-table-column min-width="86px" prop="orderStatusName" label="订单状态"></el-table-column>
        <el-table-column min-width="110px" prop="syncErpStatusName" label="同步ERP状态"></el-table-column>
        <el-table-column min-width="156px" prop="orderCreateTime" label="下单时间" :formatter="timeFormatter">
        </el-table-column>
        <el-table-column min-width="156px" prop="receiveTime" label="要求送达/自提时间" :formatter="timeFormatter">
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination v-if="total>0" class="tc mt20" background @size-change="changeSize" @current-change="changeCurrent"
        :current-page.sync="currentPage" :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import config from '@/utils/config'
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import exportSheet from '@/utils/exportExcel'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore'
  const orderMgtUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 订单查询接口
    orderQuery: '/plateform/a/order/orderQuery',
    // 获取订单状态
    getOrderStatus: '/plateform/a/order/getOrderStatus',
    // 获取平台来源
    getPlatforms: '/plateform/a/order/getPlatforms',
    // 订单导出
    exportOrder: '/plateform/a/order/exportOrder',
    exportOrderForGoods: '/plateform/a/order/exportOrderWithItem',
    // ERP落单失败重试接口
    sync: '/plateform/a/erp/possale/sync',
    // 获取所属团长店信息
    getStoreLeaderList: '/foundation/p/apply/leader/queryList'
  }
  export default {
    data() {
      return {
        loading: false,
        currentPage: 1,
        pageSize: 20,
        total: 0,
        pageSizes: config.pageSizes20,
        searchContent: {
          storeLeaderId: '',
          storeGroupTypeNo: '',
          storeGroupNo: '',
          platformId: '', // 订单来源
          beginTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 00:00:00`, // 开始时间
          endTime: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`, // 结束时间
          defaultRange: [`${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 00:00:00`, `${utils.timestampToTimeNoHMS(Date.parse(new Date()))} 23:59:59`],
          orderStatus: '', // 订单状态
          businessType: '', // 业务类型
          orderType: '', // 配送类型
          erpStoreNo: '', // 门店编码
          syncErpStatus: '', // 同步erp状态
          // 根据ERP编码或者商品名称来搜索
          searchValue: '',
          // 退款类型
          refundType: '',
          // 开始提货、配送日期
          receiveTimeStart: '',
          rTel: '', // 收货人手机号
          orderId: '', // 订单号
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '', // 城市id
          solitaireType: '' // 订单接龙类型：0-普通订单；1-接龙
        },
        // 订单来源下拉框
        platformIdOptions: [],
        // 订单状态下拉框
        orderStatusOptions: [],
        // 同步ERP状态下拉框
        syncErpStatusOptions: [{
            label: '推单失败',
            value: -1
          },
          {
            label: '待推单',
            value: 0
          },
          {
            label: '推单成功',
            value: 1
          }
        ],
        businessTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '常规商品(三方)',
            value: 1
          },
          {
            label: '预售',
            value: 3
          },
          // {
          //   label: '短周期预售',
          //   value: 2
          // },
          {
            label: '及时达',
            value: 4
          },
          {
            label: '拼团',
            value: 5
          },
          {
            label: '虚拟订单',
            value: 6
          }
        ],
        orderTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '及时送',
            value: 1
          },
          {
            label: '预约送',
            value: 2
          },
          {
            label: '自提',
            value: 3
          },
          {
            label: '快递配送',
            value: 5
          }
        ],
        tableData: [],
        // 订单导出进度loading
        exportLoading: false,
        exportConfig: {
          platformName: '订单来源',
          orderId: '订单号',
          parentOrderNo: '母单号',
          businessTypeName: '业务类型',
          orderTypeName: '配送类型',
          cityName: '城市',
          erpStoreNo: '门店编码',
          erpStoreName: '所属门店',
          storeAddress: '门店地址',
          storePhone: '门店电话',
          storeLeaderId: '团长店编号',
          virtualStoreName: '团长店名',
          userName: '团长姓名',
          rName: '收货人',
          rTel: '收货手机号',
          rAddress: '收货地址',
          orderTotalAmount: '订单金额',
          discountAmount: '优惠金额',
          realPaidAmount: '用户实付',
          refundAmount: '退款金额',
          orderStatusName: '订单状态',
          orderCreateTime: '下单时间',
          receiveTime: '期望送达/自提时间',
          buyerRemark: '订单备注'
        },
        exportLoadingForGoods: false,
        exportConfigForGoods: {
          platformName: '订单来源',
          pickSort: '提货序号',
          orderId: '订单号',
          orderCreateTime: '下单时间',
          receiveTime: '期望送达/自提时间',
          businessTypeName: '业务类型',
          orderTypeName: '配送类型',
          orderStatusName: '订单状态',
          cityName: '城市',
          erpStoreNo: '门店编码',
          erpStoreName: '所属门店',
          storeAddress: '门店地址',
          storePhone: '门店电话',
          storeLeaderId: '团长店编号',
          virtualStoreName: '团长店名',
          userName: '团长姓名',
          rName: '收货人',
          rTel: '收货手机号',
          rAddress: '收货地址',
          orderTotalAmount: '订单金额',
          discountAmount: '优惠金额',
          deliveryAmount: '运费',
          realPaidAmount: '用户实付',
          refundAmountAll: '退款金额',
          buyerRemark: '订单备注',
          erpSkuNo: 'ERP编码',
          goodsSourceTypeDesc: '商品类型',
          activityTypeDesc: '活动类型',
          activityId: '活动ID',
          erpSkuName: 'ERP商品名称',
          skuNo: 'sku编码',
          goodsName: '商品名称',
          outerProductSpec: '规格',
          quantity: '数量',
          dispPrice: '单价',
          amount: '商品金额',
          disAmount: '商品优惠金额',
          saleAmount: '合计商品金额',
          refundQuantity: '已退数量',
          refundAmount: '已退金额'
        },
        // 全选选中的内容
        checkedList: [],
        refundTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '无退款',
            value: 0
          },
          {
            label: '部分退款',
            value: 1
          },
          {
            label: '全额退款',
            value: 2
          },
          {
            label: '差额退款',
            value: 3
          }
        ],
        storeGroupTypeNoOptions: [],
        defaultProps: {
          children: 'storeGroupList',
          label: 'labelName',
          value: 'valueName'
        },
        showHignSearch: false, // 显示高级查询
        // 所属团长店
        storeLeaderOptions: [],
        solitaireTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '否',
            value: 0
          },
          {
            label: '是',
            value: 1
          }
        ]
      }
    },
    created() {
      // 获取订单状态
      this.getOrderStatus()
      // 获取订单来源
      this.getOrderOrigion()
      // 获取所属团长店信息
      this.getStoreLeaderList()
    },
    mounted() {
      let searchContentMsg = Storage.get("searchContent")
      if (this.$route.meta.keepAlive && searchContentMsg) {
        this.currentPage = searchContentMsg.currentPage
        let from = searchContentMsg.from
        for(let k in searchContentMsg.searchContent) {
          this.searchContent[k] = searchContentMsg.searchContent[k]
        }
        if(from === 'storeSum' || from === 'areaSum') {
          if(this.searchContent.platformId === 5) {
            this.searchContent.platformId = 50
          }else if(this.searchContent.platformId === 6) {
            this.searchContent.platformId = 60
          }
        }
      }
      Storage.remove('searchContent')
      this.doSearchHandel()
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.searchContent.cityId = params.cityId
        this.searchContent.erpStoreNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.currentPage = value
        this.doSearchHandel()
      },
      /**
       * [getOrderStatus 获取订单状态]
       * @return {[type]} [description]
       */
      getOrderStatus() {
        this.$http.post(orderMgtUrl.getOrderStatus, {DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            if (res.data && res.data.length) {
              this.orderStatusOptions = res.data
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取订单状态):", e.message)
        })
      },

      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        if(!this.searchContent.defaultRange) {
          this.$message.warning("请选择下单时间范围")
          return
        }
        let beginTimeVal = this.searchContent.beginTime ? new Date(this.searchContent.beginTime.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.endTime ? new Date(this.searchContent.endTime.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.beginTime && this.searchContent.endTime && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if ((endTimeVal - beginTimeVal) > 90 * 86400000 ) {
            this.$message.warning("下单开始时间至结束时间不能超过90天")
            return
          }
        }
        this.checkedList = []
        this.loading = true
        if (_param === true) {
          this.currentPage = 1
        }
        this.$http.post(orderMgtUrl.orderQuery, {
          ...this.searchContent,
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          DIFFCITY: true
        }).then(res => {
          this.loading = false
          if (res.code === 0) {
            if (res.data && res.data.result && res.data.result.length) {
              res.data.result.forEach(item => {
                item.redirectUrl = `/order_mgt/detail/${item.orderId}`
              })
              this.tableData = res.data.result
              let pageNo = this.currentPage
              this.currentPage = pageNo
            } else {
              this.tableData = []
            }

            this.total = res.data.total
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [showDetail 查看详情]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showDetail(_row) {
        // 设置搜索条件
        let _paramMesg = {
          searchContent: this.searchContent,
          currentPage: this.currentPage
        }

        Storage.set("searchContent", _paramMesg)
        // 去详情页
        this.$router.push("/order_mgt/detail/" + _row.orderId)
      },
      /**
       * [getOrderOrigion 获取订单来源]
       * @return {[type]} [description]
       */
      getOrderOrigion() {
        this.$http.post(orderMgtUrl.getPlatforms, {DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            if (res.data && res.data.length) {
              let platformItem = {
                "id": null,
                "name": "全部",
                "level": null,
                "desc": null,
                "platformId": '',
                "totalAmount": null
              }
              res.data.unshift(platformItem)
              this.platformIdOptions = res.data
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取订单来源):", e.message)
        })
      },
      /**
       * [timeFormatter 时间格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      timeFormatter(row, column, cellValue) {
        return cellValue && cellValue != "" ? utils.timestampToTimeYMDHM(new Date(cellValue.replace(/-/g, '/'))
        .getTime()) : ''
      },
      /**
       * [moneyToFixed 金额格格式化]
       * @param  {[type]} row       [description]
       * @param  {[type]} column    [description]
       * @param  {[type]} cellValue [description]
       * @return {[type]}           [description]
       */
      moneyToFixed(row, column, cellValue) {
        return cellValue && cellValue != "" ? parseFloat(cellValue).toFixed(2) : '0.00'
      },
      businessTypeNameFormater(row, column, cellValue) {
        return cellValue && cellValue != "" ? cellValue : '--'
      },
      /**
       * [exportData 导出订单]
       * @return {[type]} [description]
       */
      exportData() {
        if(!this.searchContent.defaultRange) {
          this.$message.warning("请选择下单时间范围")
          return
        }
        let beginTimeVal = this.searchContent.beginTime ? new Date(this.searchContent.beginTime.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.endTime ? new Date(this.searchContent.endTime.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.beginTime && this.searchContent.endTime && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if ((endTimeVal - beginTimeVal) > 90 * 86400000 ) {
            this.$message.warning("下单时间范围开始时间至结束时间不能超过90天")
            return
          }
        }
        this.exportLoading = true
        this.$http.post(orderMgtUrl.exportOrder, {
          ...this.searchContent,
          DIFFCITY: true
        }).then(res => {
          this.exportLoading = false
          if (res.code === 0) {
            if (res.data && res.data.length) {
              let tableDataArr = res.data
              const tHeader = utils.objectToArray(this.exportConfig, false)
              const filterVal = utils.objectToArray(this.exportConfig, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tableDataArr,
                filename: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_订单查询`
              })
              this.$notify.success({
                title: '温馨提示',
                message: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_订单查询导出成功`
              });
            } else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoading = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      },
      /**
       * [exportOrderAndGoodsData 导出带商品的订单]
       * @return {[type]} [description]
       */
       exportOrderAndGoodsData() {
        if(!this.searchContent.defaultRange) {
          this.$message.warning("请选择下单时间范围")
          return
        }
        let beginTimeVal = this.searchContent.beginTime ? new Date(this.searchContent.beginTime.replace(/-/g, '/')).getTime() : 0
        let endTimeVal = this.searchContent.endTime ? new Date(this.searchContent.endTime.replace(/-/g, '/')).getTime() : 0
        if (this.searchContent.beginTime && this.searchContent.endTime && !isNaN(beginTimeVal) && !isNaN(endTimeVal)) {
          if ((endTimeVal - beginTimeVal) > 90 * 86400000 ) {
            this.$message.warning("下单时间范围开始时间至结束时间不能超过90天")
            return
          }
        }
        this.exportLoadingForGoods = true
        this.$http.post(orderMgtUrl.exportOrderForGoods, {
          ...this.searchContent,
          DIFFCITY: true
        }).then(res => {
          this.exportLoadingForGoods = false
          if (res.code === 0) {
            if (res.data && res.data.length) {
              let tableDataArr = res.data
              const tHeader = utils.objectToArray(this.exportConfigForGoods, false)
              const filterVal = utils.objectToArray(this.exportConfigForGoods, true)
              exportSheet.ExportJsonToExcel({
                header: tHeader,
                filterVal,
                data: tableDataArr,
                filename: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_订单商品`
              })
              this.$notify.success({
                title: '温馨提示',
                message: `${utils.timestampToTimeNoHMS(Date.parse(new Date()))}_订单商品导出成功`
              });
            } else {
              this.$notify.warning({
                title: '温馨提示',
                message: '没有查询到要导出的数据'
              });
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.exportLoadingForGoods = false
          this.$notify.error({
            title: '提示(导出数据)：',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if (utils.isKeyInArray(row.orderId, this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('orderId', row.orderId, this.checkedList)
          return
        } else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if (val.length > 0) {
          // 全选
          for (let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
            return item
          }, [])
        } else {
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for (let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'orderId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : []
        }
      },
      /**
       * [doRePushHandel 重新推送]
       * @return {[type]} [description]
       */
      doBatchRePushHandel() {
        if (this.checkedList.length === 0) {
          this.$message.warning("请选择要推送的订单")
          return
        }
        utils.showLoading()
        let params = []
        for (let item of this.checkedList) {
          params.push(item.orderId)
        }
        this.$http.post(orderMgtUrl.sync, {
          orderIdList: params,
          DIFFCITY: true
        }).then(res => {
          utils.clearLoading()
          if (res.code === 0) {
            this.$message.success("订单重新推送成功")
            // 成功之后刷新一下页面
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          // 清除全局loading
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 获取所属团长店信息
       * @param {type} 
       * @return: 
       */
      getStoreLeaderList() {
        this.$http.post(orderMgtUrl.getStoreLeaderList, {pageNum: 1, pageSize: -1, DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.storeLeaderOptions = optionsArr.filter(item => {
              return item.applyStatus === 2 && item.userStatus === 0
            }).map(item => {
              return {
                id: item.id,
                applyStatus: item.applyStatus,
                virtualStoreName: item.virtualStoreName,
                userName: item.userName,
                actualStoreNo: item.actualStoreNo,
                value: item.id,
                label: `${item.id}_${item.virtualStoreName}_${item.userName}`
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取所属团长店信息):", e.message)
        })
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.beginTime = result ? result[0] : ''
        this.searchContent.endTime = result ? result[1] : ''
        this.doSearchHandel(true)
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave(to, from, next) {
      from.meta.keepAlive = false;
      next();
    }

  }

</script>



